import type { MenuProps } from './types'
import { Container, Flex, Link, Text, Box } from '~/ui/primitives'
import { toRem } from '~/styles'

export default function DesktopMenu({ menuItems }: MenuProps) {
  return (
    <Container
      as="nav"
      display={{ xs: 'hidden', lg: 'flex' }}
      position="fixed"
      bottom={86}
      right={{ xs: '0', lg: 40, xl: `calc(((100vw - ${toRem(1280)})/2) - ${toRem(28)})` }}
      paddingTop={{ lg: 86 }}
      zIndex="z50"
    >
      <Flex direction="col" align="end">
        {menuItems.map((item) => (
          <Link key={item} href={`#${item}`} position="relative" variant="menu" paddingY={5}>
            <Text>{item}</Text>
          </Link>
        ))}
      </Flex>
    </Container>
  )
}
